body {
    background-color: $primary-bg-color;
    color: $primary-font-color;
    font-family: $primary-font-family;
    line-height: 1.2;
}

.page {
    margin: 0 auto;
    width: 85%;
}

// clearfix class
.cf {
    *zoom: 1;
    &:before, &:after {
        content: " ";
        display: table;
    }
    &:after {
        clear: both
    }
}
